  components:{
    Header
  },
   updated(){
    
      this.sliderinterval();
    
  },
  mounted(){
  
      this.muinit();
      this.getNews();
    
  }
  ,
 
  methods: {
    muinit() { 
      mui.init({});
      this.sliderinterval();
      mui.ready(function() {
        mui(".mui-scroll-wrapper").scroll({
          bounce: true //
        });
      });
    },
    getNews(){
      const root = process.env.OPEN_HOST;
    // https://3g.163.com/touch/reconstruct/article/list/BDC4QSV3wangning/0-10.html
        this.$http.get(root+'/touch/reconstruct/article/list/BDC4QSV3wangning/0-10.html')
                 .then(result=>{
                   let str = result.body.substring(9,result.body.length-1)
                   this.newslist = JSON.parse(str).BDC4QSV3wangning;
            },error=>{
              console.log(error)
            }).catch(err => {
          console.log(err)
          })

    },
    sliderinterval() {
      //
      var gallery = mui(".mui-slider");
      gallery.slider({
        interval: 2000 //00
      });
    },
    toLink(t){
      event.currentTarget.children[0].click();
    }

  }
};
