/**
 * options:{
 *  url           // 
 *  method        // 
 *  params        // url
 *  data          // post
 *  formData      // Content-Typefalse'application/json;charset=UTF-8'
 *  isShowLoading // loadingfalse
 *  isDownload    // false
 * }
 */
class Ajax {
    static post(options) {
        let isShowLoading=options.isShowLoading || false
        if (isShowLoading) startLoading()
        return new Promise((resolve, reject) => {
            Axios({
                url: options.url,
                method: 'POST',
                params: options.params|| '',
                data: options.data || '',
                formData:options.formData||false,
                isDownload:options.isDownload|| false
            }).then((response) => {
                if (isShowLoading) endLoading()
                if (response.status === 200) {
                    resolve(response.data)
                    return
                }
                reject(response)
            }).catch(err => {
                if (isShowLoading) endLoading()
                reject(err)
            })
        })
    }
    // GET
    static get(options) {
        let isShowLoading=options.isShowLoading || false
        if (isShowLoading) startLoading()
        return new Promise((resolve, reject) => {
            Axios({
                url: options.url,
                method: 'GET',
                params: options.params || '',
                isDownload:options.isDownload|| false
            }).then(response => {
                if (isShowLoading) endLoading()
                if (response.status === 200) {
                    resolve(response.data)
                    return
                }
                reject(response)
            }).catch(err => {
                if (isShowLoading) endLoading()
                reject(err)
            })
        })
    }
}

export default Ajax


